<template>
  <div v-if="loading">
    <SceletonGridContainer :count-items="items.length" />
  </div>
  <div v-else class="table-responsive">
    <table class="table table-hover ithit-grid-container">
      <thead>
        <tr>
          <th scope="col">
            <label class="custom-checkbox"
              ><input v-model="allSelected" type="checkbox"/><span
                class="checkmark"
            /></label>
          </th>
          <th scope="col" />
          <TableSortHeader
            class="ellipsis sort"
            scope="col"
            sort-column="displayname"
          >
            <span>Display Name</span>
          </TableSortHeader>
          <TableSortHeader
            class="d-none d-xl-table-cell sort"
            scope="col"
            sort-column="getcontenttype"
          >
            Type
          </TableSortHeader>
          <TableSortHeader
            class="sort"
            scope="col"
            sort-column="quota-used-bytes"
          >
            Size
          </TableSortHeader>
          <TableSortHeader
            class="d-none d-lg-table-cell sort"
            scope="col"
            sort-column="getlastmodified"
          >
            Modified
          </TableSortHeader>
          <th class="column-action" scope="col" />
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in items">
          <tr :key="index">
            <td class="select-disabled">
              <label class="custom-checkbox"
                ><input
                  v-model="item.Selected"
                  type="checkbox"
                  @change="manageSelectedItem(item)"/><span class="checkmark"
              /></label>
            </td>
            <td>
              <span :class="{ 'icon-folder': item.IsFolder }">
                <span
                  v-if="item.ActiveLocks.length"
                  class="ithit-grid-icon-locked"
                >
                  <span
                    v-if="item.ActiveLocks[0].LockScope === 'Shared'"
                    class="badge badge-pill badge-dark"
                    >{{ item.ActiveLocks.length }}</span
                  >
                </span>
              </span>
            </td>
            <td v-if="item.IsFolder" class="ellipsis">
              <router-link :to="openFolder(item.Href)">
                {{ item.DisplayName }}
              </router-link>
            </td>
            <td v-else class="ellipsis">
              <span>{{ item.DisplayName }}</span>
            </td>
            <td class="d-none d-xl-table-cell">
              <span v-if="item.IsFolder">Folder</span>
              <span v-else>File {{ getFileExtension(item.DisplayName) }}</span>
            </td>
            <td class="text-right">
              <span v-if="!item.IsFolder">{{
                formatFileSize(item.ContentLength)
              }}</span>
            </td>
            <td class="d-none d-lg-table-cell modified-date">
              {{ formatDate(item.LastModified) }}
            </td>
            <td class="text-right select-disabled position-relative">
              <ItemActions :item="item" />
            </td>
          </tr>
          <tr v-if="searchMode" :key="index" class="tr-snippet-url">
            <td class="d-none d-xl-table-cell" />
            <td class="d-none d-lg-table-cell" />
            <td colspan="10">
              <SnippetSearch :item="item" />
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </div>
</template>
<script>
import CommonService from "@/services/CommonService.js";
import UrlResolveService from "@/services/UrlResolveService.js";
import ItemActions from "@/components/grid/ItemActions.vue";
import SnippetSearch from "@/components/SnippetSearch.vue";
import SceletonGridContainer from "@/components/grid/SceletonGridContainer.vue";
import TableSortHeader from "@/components/grid/TableSortHeader.vue";
export default {
  components: {
    ItemActions,
    SnippetSearch,
    SceletonGridContainer,
    TableSortHeader
  },
  data() {
    return {
      allSelected: false
    };
  },
  computed: {
    items() {
      return this.$store.state.Items;
    },
    searchMode() {
      return this.$store.state.SearchQuery != "";
    },
    loading() {
      return this.$store.state.GridLoading;
    }
  },
  watch: {
    allSelected: function(value) {
      this.setSelectedForAllItems(value);
    },
    items: function() {
      this.allSelected = false;
      this.setSelectedForAllItems(false);
      this.$store.commit("setGridLoading", false);
    }
  },
  created() {
    this.setSelectedForAllItems(false);
    this.$store.commit("setGridLoading", false);
  },
  methods: {
    /**
     * Gets file extension
     * @param {string} fileName file name with extension
     * @returns {string} file extension
     */
    getFileExtension(fileName) {
      return CommonService.Formatters.GetFileExtension(fileName);
    },
    /**
     * Formats file size
     * @param {number} fileSize file size
     * @returns {string} formated file size
     */
    formatFileSize(fileSize) {
      return CommonService.Formatters.FileSize(fileSize);
    },
    /**
     * Formats date
     * @param {Date} date date
     * @returns {string} formated date
     */
    formatDate(date) {
      return CommonService.Formatters.Date(date);
    },
    /**
     * Opens folder
     * @param {String} sUrl Must be full path including domain name
     */
    openFolder(sUrl) {
      return UrlResolveService.getRelativeUrl(sUrl);
    },
    /**
     * Set 'Selected' property for all items
     * @param {Boolean} isSelected
     */
    setSelectedForAllItems(isSelected) {
      this.items.forEach(element => {
        element.Selected = isSelected;
      });
      this.$store.commit("clearSelectedItems");
      if (isSelected) {
        this.items.forEach(element => {
          this.$store.commit("pushToSelectedItems", element);
        });
      }
    },
    /**
     * Add or remove item to selected items array
     * @param {HierarchyItem} item
     */
    manageSelectedItem(item) {
      if (item.Selected) {
        this.$store.commit("pushToSelectedItems", item);
      } else {
        this.$store.commit("removeFromSelectedItems", item);
      }
    }
  }
};
</script>
